import React, { useState } from "react"
import { Menu, Icon, Sidebar, Segment, Grid } from "semantic-ui-react"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import Layout from "./layout"

const MobilePage = ({ options, content, prev, next }) => {
  const [menuVisibility, setMenuVisibility] = useState(false)

  const toggleMenu = () => setMenuVisibility(!menuVisibility)

  return (
    <Layout>
      <Menu inverted style={{ borderRadius: 0, margin: 0 }}>
        <Menu.Item name="menu" onClick={toggleMenu}>
          <Icon name="content" />
        </Menu.Item>
      </Menu>

      <Sidebar.Pushable
        as={Segment}
        style={{
          height: `${document.documentElement.clientHeight - 40}px`, // 40px menu heigth
          border: "none",
          margin: 0,
          borderRadius: 0,
          boxShadow: "none",
        }}
      >
        <Sidebar
          as={Menu}
          animation="push"
          inverted
          vertical
          visible={menuVisibility}
        >
          {options()}
        </Sidebar>
        <Sidebar.Pusher>
          <Grid padded>
            <Grid.Row>
              <Grid.Column>{content(toggleMenu)}</Grid.Column>
            </Grid.Row>
            <Grid.Row
              columns="equal"
              style={{ position: "absolute", bottom: 0 }}
            >
              <Grid.Column textAlign="left">
                {prev && (
                  <AniLink swipe direction="right" to={prev}>
                    <Icon
                      name="angle left"
                      size="huge"
                      style={{
                        color: "#3D4868",
                      }}
                    />
                  </AniLink>
                )}
              </Grid.Column>
              <Grid.Column textAlign="right">
                {next && (
                  <AniLink swipe direction="left" to={next}>
                    <Icon
                      name="angle right"
                      size="huge"
                      style={{
                        color: "#3D4868",
                      }}
                    />
                  </AniLink>
                )}
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </Layout>
  )
}

export default MobilePage
